.header {
    max-width: 600px;
    width: 100%;
    margin: 0 auto;
}

.caption {
    color: #11175E;
    font-family: Rubik, sans-serif;
    font-weight: 600;
    font-size: 4.8rem;
    letter-spacing: 0.05rem;
}

@media (max-width: 640px) {
    .header {
        display: flex;
        justify-content: center;
    }
}